import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login/index";
import Home from "../views/Home/index";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/login",
    name: "login",
    component: Login
  },
  {
    path: "/home",
    name: "home",
    component: Home
  }
];

const router = new VueRouter({
  routes
});

export default router;
